$colorGray: rgba(170, 170, 170, 1)
$colorDarkGray: rgba(136, 136, 136, 1)
$colorMiddleGray: rgba(68, 68, 68, 1)
$colorWhite: rgba(255, 255, 255, 1)
$colorBlack: rgba(34, 34, 34, 1)
$colorBlue: rgba(59, 153, 252, 0.7)
$colorDarkGreen: rgba(0,0,0,.04)
$colorMilk: rgba(229, 229, 229, 1)

.select, .selectError
  display: block
  font-size: 16px
  font-family: sans-serif
  font-weight: 700
  color: $colorMiddleGray
  line-height: 1.3
  padding: .6em 1.4em .5em .8em
  width: 100%
  max-width: 100%
  box-sizing: border-box
  margin: 0
  border: 3px solid $colorGray
  box-shadow: 0 1px 0 1px $colorDarkGreen
  border-radius: .5em
  -moz-appearance: none
  -webkit-appearance: none
  appearance: none
  background-color: $colorWhite
  background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'), linear-gradient(to bottom, $colorWhite 0%, $colorMilk 100%)
  background-repeat: no-repeat, repeat
  background-position: right .7em top 50%, 0 0
  background-size: .65em auto, 100%

.selectError
  border: 3px solid red

.select::-ms-expand
  display: none

.select:hover
  border-color: $colorDarkGray

.select:focus
  border-color: $colorGray
  box-shadow: 0 0 1px 3px $colorBlue
  color: $colorBlack
  outline: none

.select option
  font-weight: normal

